import opg from 'ts/opg.ts';
import Table from 'ts/ui/Table.ts';
import {Languages} from 'ts/util/Languages';
import PopUp from '../../../ts/ui/Popup';

console.log('Languages:', Languages);

opg.api({
	getInformation: 'Information/QueryAllEnableInformations',
	'addCarousel!post': 'canrousel/AddCanrousel',
});


$('#type').on('change', function () {
	tb.update({type: $(this).val(), pageIndex: 0, pageSize: 10});
});


let tb = opg('#tbResult').table({
	api: opg.api.getInformation,
	param: {type: 0},
	titleBar: {
		title: `${Languages.package.news}${Languages.package.list}`,
	},
	columns: [
		{
			text: ' ',
			src: 'id', cmd: 'checkOne',
		},
		{
			text: `${Languages.package.news}${Languages.package.title}`,
			src: 'title',
		},
	],
	pagination: {
		pageSize: 20,
	},
});


let form = $('#tbSearch');
window['doSave'] = function (popWin: PopUp, table: Table) {
	let informationId = tb.getCheckedValue();
	if (!informationId) {
		opg.warn(`${Languages.package.nullSelectWarn}`);
		return true;
	}

	let param = form.fieldsToJson({
		title: {
			name: Languages.package.title,
			type: 'ns',
			require: true,
		},
	});


	if (!param)
		return true;

	param.informationId = informationId;

	return opg.api.addCarousel(param, function () {
		popWin.close();
		table.update();
	});
};